import styled from 'styled-components';

const BubbleDiv = styled.div`
  margin-top: 5rem;
  position: relative;
  width: 35rem;
  height: 5rem;
  background: #1696e5;
  border-radius: 1.5rem;
  color: #fff;
  padding: 1rem;
  font-size: 1.4rem;
  font-family: 'Spotlight';
  display: flex;
  justify-content: center;
  align-items: center;

  :after {
    border-top: 2rem solid #1696e5;
    border-left: 2rem solid transparent;
    border-right: 0px solid transparent;
    border-bottom: 0px solid transparent;
    content: '';
    position: absolute;
    top: 1rem;
    left: -1rem;
  }
`;
export default BubbleDiv;
