import React, { useContext, useState } from 'react';
import styled from 'styled-components';
import Container from 'components/common/Container';
import SquareButton from 'components/common/SquareButton';
import PageTitle from 'components/common/PageTitle';
import PageSubtitle from 'components/common/PageSubtitle';
import TopSection from 'components/common/TopSection';
import BottomSection from 'components/common/BottomSection';
import SimulatorContext from 'components/Simulator/context';
import BasePage from 'components/pages/BasePage';
import { datas } from 'utils/constants';
import { getRankList } from 'utils/result';
import Dialog from 'components/common/Dialog';
import DialogDetailPage from '../DialogDetailPage';

const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
`;
const Img = styled.img`
  width: 6rem;
  margin-bottom: 0.5rem;
`;
const Title = styled.div`
  font-weight: 800;
  font-family: 'Spotlight';
  font-size: 2.5rem;
`;
const SubText = styled.div`
  font-size: 1.2rem;
  margin-top: 1rem;
  color: #717578;
`;
const TitleDiv = styled.div`
  position: absolute;
  text-align: center;
  width: 100%;
  top: 4rem;
`;

const Page: React.FC = () => {
  const {
    state: { product },
    setState,
    goToNext,
  } = useContext(SimulatorContext);
  const [open, setOpen] = useState(false);
  const setProduct = (productId: number | null) =>
    setState((currentState) => {
      return {
        ...currentState,
        product: productId,
      };
    });
  return (
    <BasePage>
      <Container>
        <TopSection>
          <PageTitle>제품 고르기</PageTitle>
          <PageSubtitle>
            이번 콘테스트의 승패는 제품에 맞는 광고매체와 모델을 고르는 것에
            달려있어요. <br />
            제품을 터치하여 제품특성을 알아보고, 자신있는 제품을 선택해주세요!
          </PageSubtitle>
        </TopSection>
        <BottomSection>
          <Dialog open={open} handleClose={() => setOpen(false)}>
            <div
              style={{
                fontSize: '2rem',
                width: '100%',
                height: '100%',
                zIndex: 0,
              }}
            >
              <TitleDiv>
                <Title>제품 특성 알아보기</Title>
                <SubText>
                  제품의 특성을 읽고 누구에게 어떻게 전달되면 좋을지
                  상상해보세요. 버튼을 클릭하면 제품이 선택되고 매체 선택
                  페이지로 이동됩니다.
                </SubText>
              </TitleDiv>

              {product !== null && (
                <DialogDetailPage
                  option={datas.products[product].name}
                  contents={[
                    {
                      title: '연령대별 선호도',
                      name: `"${datas.products[product].name}" 제품은 `,
                      values: [...datas.products[product].values],
                      type: 'columnChart',
                    },
                    {
                      title: '키워드',
                      name: datas.products[product].name,
                      values: [...datas.products[product].keywords],
                      type: 'bubbleChart',
                    },
                  ]}
                  goToNextPage={goToNext}
                />
              )}
            </div>
          </Dialog>
          <Grid>
            {datas.products.map((productItem) => (
              <SquareButton
                key={productItem.id}
                color={productItem.color}
                onClick={() => {
                  setProduct(productItem.id);
                  setOpen(true);

                  // eslint-disable-next-line no-console
                  console.log(
                    productItem.name,
                    '의 랭크 정보 : ',
                    getRankList(productItem.id),
                  );
                }}
              >
                <div>
                  <Img src={productItem.imgSrc} alt="" />
                </div>
                {productItem.name}
              </SquareButton>
            ))}
          </Grid>
        </BottomSection>
      </Container>
    </BasePage>
  );
};

export default Page;
