import React from 'react';
import styled, { css } from 'styled-components';
import { Theme, defaultShadow, ColorType } from 'styles/theme';

type SizeType = 'medium' | 'large';
type StyledButtonProps = {
  theme: Theme;
  color: ColorType;
  backImg: string;
  size: SizeType;
};

const backgroundStyles = css`
  ${({ theme, color, backImg }: StyledButtonProps) =>
    css`
      background-color: ${theme.palette[color]};
      background-image: url(${backImg});
    `}
`;

const sizes = {
  large: {
    width: '22rem',
    height: '22rem',
  },
  medium: {
    width: '17rem',
    height: '17rem',
  },
};

const sizeStyles = css`
  ${({ size }: StyledButtonProps) =>
    css`
      width: ${sizes[size].width};
      height: ${sizes[size].height};
    `}
`;

const StyledButton = styled.button<StyledButtonProps>`
  /* common style */
  border-radius: 2rem;
  color: #fff;
  font-size: 2rem;
  font-family: 'Spotlight', sans-serif;
  margin: 1rem;
  outline: 0;
  border: 0;
  cursor: pointer;
  box-shadow: ${defaultShadow};
  background-size: cover;

  ${backgroundStyles};
  ${sizeStyles}
`;

type ButtonProps = {
  color?: ColorType;
  backImg?: string;
  size?: SizeType;
  onClick?: () => void;
};

const SquareButton: React.FC<ButtonProps> = ({
  children,
  color = 'lightGray',
  backImg = '',
  size = 'medium',
  ...rest
}) => {
  return (
    <StyledButton backImg={backImg} size={size} color={color} {...rest}>
      {children}
    </StyledButton>
  );
};

export default SquareButton;
